@import url(https://fonts.googleapis.com/css?family=Montserrat:200,300,400,500,700&display=swap);
body{margin:0;padding:0;font-family:'Montserrat', sans-serif;letter-spacing:1px}*{font-family:inherit;box-sizing:border-box;outline:none}

header{position:relative;z-index:10;width:100%;height:50px;background:linear-gradient(107deg, #4d76ff 0%, #0f3f8c 100%);display:flex;align-items:center;padding:0 30px}header a.brand{display:flex;align-items:center;text-decoration:none;color:white}header a.brand img{height:32px;margin:0 15px 5px 0}header a.brand h1{color:white;font-size:24px;font-weight:300;letter-spacing:2px;margin:0 0 2.5px 0}@media (max-width: 640px){header a.brand img{height:24px}header a.brand h1{display:none}}

div.container{width:100%;min-height:calc(100vh - 50px)}div.page-wrap{width:100%;max-width:1200px;min-height:calc(100vh - 50px);margin:auto;padding:15px}div.page-wrap h2,div.page-wrap h3,div.page-wrap h4,div.page-wrap h5{font-weight:400}

div.page-wrap.page-examples{display:flex;padding:0;max-width:100%;height:auto;max-height:none}@media (max-width: 640px){div.page-wrap.page-examples{display:block}}div.page-wrap.page-examples div.page-column h2,div.page-wrap.page-examples div.page-column h3{margin:0;padding:0 0 15px 0}div.page-wrap.page-examples div.page-column h2 span.bold,div.page-wrap.page-examples div.page-column h3 span.bold{font-weight:bold;margin:0 15px 0 0}div.page-wrap.page-examples div.page-column.table-of-contents{position:fixed;left:0;top:0;width:320px;height:100%;background:#333;z-index:9;padding:15px 15px 15px 15px}div.page-wrap.page-examples div.page-column.table-of-contents h3{padding-top:65px !important}@media (max-width: 640px){div.page-wrap.page-examples div.page-column.table-of-contents h3{padding-top:15px !important}}div.page-wrap.page-examples div.page-column.primary{width:100%;max-width:1500px;padding:15px 15px 480px 335px}@media (max-width: 640px){div.page-wrap.page-examples div.page-column{position:static !important;padding:0px 0px 15px 0 !important;width:100% !important}}div.page-wrap.page-examples div.page-column.table-of-contents{padding:0}div.page-wrap.page-examples div.page-column.table-of-contents h3{font-size:18px;font-weight:bold;color:white;padding:15px}div.page-wrap.page-examples div.page-column.table-of-contents a.link{display:flex;align-items:center;width:100%;height:40px;padding:0 15px;color:white;cursor:pointer;font-size:14px;-webkit-transition:opacity 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53);transition:opacity 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53)}div.page-wrap.page-examples div.page-column.table-of-contents a.link:hover{background:#404040}div.page-wrap.page-examples div.page-column.table-of-contents a.link.active{background:#4d4d4d}div.page-wrap.page-examples div.page-column.table-of-contents a.link span.yellow{font-weight:bold;color:#FFFFCC;margin:0 15px 0 0}@media (max-width: 640px){div.page-wrap.page-examples div.page-column.table-of-contents a.link{font-size:12px}}div.page-wrap.page-examples div.guide-content{width:100%;padding:15px 15px 45px 15px}div.page-wrap.page-examples div.guide-content h2{margin:0;padding:0 0 15px 0}div.page-wrap.page-examples div.guide-content h2.title{font-weight:bold}div.page-wrap.page-examples div.guide-content h3.subtitle{font-style:italic;padding:0 0 60px 0}@media (max-width: 640px){div.page-wrap.page-examples div.guide-content h2{font-size:18px}div.page-wrap.page-examples div.guide-content h3{font-size:16px}}div.page-wrap.page-examples div.guide-content p{font-size:16px;line-height:1.5;margin:0;padding:0 0 15px 0}@media (max-width: 640px){div.page-wrap.page-examples div.guide-content p{font-size:14px}}div.page-wrap.page-examples div.guide-content div.react-codemirror2{width:100%;height:auto;overflow:hidden;border-radius:8px;margin:0 0 15px 0}div.page-wrap.page-examples div.guide-content div.react-codemirror2 div.CodeMirror{height:100%;padding:10px 5px}div.page-wrap.page-examples div.guide-content div.guide-buttons{display:flex;align-items:center;width:100%;height:45px;margin:0 0 15px 0}div.page-wrap.page-examples div.guide-content div.guide-buttons a.guide-button{display:flex;align-items:center;justify-content:center;background:linear-gradient(107deg, #4d76ff 0%, #0f3f8c 100%);color:white;width:180px;height:30px;border-radius:5px;margin:0 15px 0 0;opacity:0.75;cursor:pointer;font-size:12px;font-weight:bold;text-decoration:none;-webkit-transition:opacity 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53);transition:opacity 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53)}div.page-wrap.page-examples div.guide-content div.guide-buttons a.guide-button i{margin:0 10px 0 0}div.page-wrap.page-examples div.guide-content div.guide-buttons a.guide-button:hover,div.page-wrap.page-examples div.guide-content div.guide-buttons a.guide-button.active{opacity:1}

